<template>
  <div
    class="part2Index"
    @mousewheel="handleMousewhell"
    @click="visibleIndex++"
  >
    <div class="Box1" :class="visibleIndex === 0 && 'visible'">
      <div class="imgBox1">
        <img src="@/assets/buildInfo/build3/天-15.png" alt="" class="img1" />
      </div>
      <div class="content1">
        <p style="font-family: QingKai" class="content1P1">
          这里有店主从各地搜罗来的小玩意，还有从泰国带回来的这尊木佛像，让这有历史情怀的小楼又增添一份文艺的调调。
        </p>
      </div>
    </div>
    <div class="Box2" :class="visibleIndex === 1 && 'visible'">
      <div class="imgBox2">
        <img src="@/assets/buildInfo/build3/天-16.png" alt="" class="img2" />
      </div>
      <div class="content2">
        <p style="font-family: QingKai" class="content2P1">这是国内少有的</p>
        <p style="font-family: QingKai" class="content2P1">开放式厨房的餐厅</p>
        <p style="font-family: QingKai" class="content2P1">
          餐厅中间的天花板镂空，
        </p>
        <p style="font-family: QingKai" class="content2P1">
          分上下两层，把这八米高的房子设计成
        </p>
      </div>
    </div>
    <div class="Box3" :class="visibleIndex === 2 && 'visible'">
      <div class="imgBox3">
        <img src="@/assets/buildInfo/build3/天-17.png" alt="" class="img3" />
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  data() {
    return {
      visibleIndex: 0,
    };
  },
  components: {},
  mounted() {},
  methods: {
    handleMousewhell(e) {
      if (e.wheelDelta > 0) {
        //   上滚
        if (this.visibleIndex === 0) {
          //    在这里跳转上一个页面
        } else {
          this.visibleIndex--;
        }
      } else {
        //   下滚
        if (this.visibleIndex === 2) {
          // 在这里跳转下一个页面
        } else {
          this.visibleIndex++;
        }
      }
    },
  },
};
</script>

<style scoped>
.part2Index {
  background-color: #e2e3d2;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.Box1,
.Box2,
.Box3 {
  width: 100%;
  height: 100vh;
  position: absolute;
  transition: 1s;
  opacity: 0;
}
.imgBox1,
.imgBox2,
.imgBox3 {
  width: 100vw;
  height: 100vh;
  position: absolute;
  z-index: 10;
}
.img1,
.img2,
.img3 {
  width: 100%;
  height: 100%;
}

.visible {
  opacity: 1;
}

.content1,
.content2 {
  width: 95%;
  height: 100vh;
  text-align: left;
  position: absolute;
  left: 50%;
  top: 0;
  transform: translateX(-50%);
  z-index: 20;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
}
.content1P1,
.content2P1 {
  font-size: 7.8rem;
  transition: 1s;
  color: white;
  text-align: left;
  letter-spacing: 0px;
}
</style>
